import { AlertService } from "./alert.service";
import {
  FormValidationMessageField,
  FormValidationType,
  FormValidationMessage,
} from "src/app/models/from-validation-message";
import { Injectable, OnDestroy } from "@angular/core";
import { FormGroup, ValidationErrors, FormControl, ValidatorFn } from "@angular/forms";
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Injectable({
  providedIn: "root",
})
export class FormHelperService {
  constructor(private alert: AlertService) {}
  validateForm(
    validationMessages: FormValidationMessageField[],
    form: FormGroup
  ): boolean {
    for (let key of Object.keys(form.controls)) {
      const controlErrors: ValidationErrors = form.get(key).errors;
      if (controlErrors != null) {
        for (let keyError of Object.keys(controlErrors)) {
          this.alert.presentError(
            validationMessages
              .find((v) => v.name == key)
              .validations.find((m) => m.type == keyError).message
          );
          return false;
        }
      }
    }
    return true;
  }
  addValidationMessage(
    messagesObject: FormValidationMessageField[],
    controlName: string,
    validationType: FormValidationType,
    message: string
  ) {
    let field = messagesObject.find((o) => o.name == controlName);
    if (!field) {
      field = new FormValidationMessageField();
      field.name = controlName;
      field.validations = [];
      messagesObject.push(field);
    }
    let validation = field.validations.find((v) => v.type == validationType);
    if (!validation) {
      validation = new FormValidationMessage();
      validation.type = validationType;
      field.validations.push(validation);
    }
    validation.message = message;
  }
  public setConditionalValidators(control:FormControl,
    controlToObserveValue:FormControl,
    condition:boolean | null,
    validator:ValidatorFn|ValidatorFn[],
    cleanUpComponent: OnDestroy ){
    

    if (cleanUpComponent) {
      let cleanUpSubject = new Subject();
      const originalDestroy = cleanUpComponent.ngOnDestroy;
      cleanUpComponent.ngOnDestroy = () => {
        originalDestroy.apply(cleanUpComponent, arguments);
        cleanUpSubject.next();
        cleanUpSubject.complete();
      };
      
      controlToObserveValue.valueChanges.pipe(takeUntil(cleanUpSubject)).subscribe((result)=>{
       
        if((condition !== null && condition) || result )
        {
          control.setValidators(
            validator
          );
        }
        else
        {
          control.clearValidators();
        }
        control.updateValueAndValidity();
      });
    }

   
  }
}
