import { AvailableHours } from "./../models/available-hours";
import { HttpClient } from "@angular/common/http";
import { concatMap, map, tap } from "rxjs/operators";
import { from, Subject, of } from "rxjs";
import { AuthService } from "src/app/services/auth.service";
import { EnvService } from "src/app/services/env.service";
import { Injectable } from "@angular/core";

@Injectable({
  providedIn: "root",
})
export class CalendarService {
  constructor(
    private env: EnvService,
    private auth: AuthService,
    private http: HttpClient
  ) {}
  availableHoursArray: AvailableHours[] = [];
  availableHoursUpdated: Subject<AvailableHours[]> = new Subject<
    AvailableHours[]
  >();
  addAvailableHours(
    timeFrom: string,
    timeTo: string,
    repeat: boolean,
    repeatingDayOfWeek: number
  ) {
    let hours = new AvailableHours();
    hours.time_from = timeFrom;
    hours.time_to = timeTo;
    hours.repeating_day_of_week = repeatingDayOfWeek;
    if (this.checkForOverlap(this.availableHoursArray, hours)) {
      return of(false);
    }
    let url = "calendar/addAvailableHours";
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        return this.http
          .post(
            this.env.API_URL + url,
            {
              time_from: timeFrom,
              time_to: timeTo,
              repeating_day_of_week: repeatingDayOfWeek,
            },
            {
              headers: {
                Authorization:
                  token["token_type"] + " " + token["access_token"],
                Accept: "application/json",
                "Content-Type": "application/json",
              },
            }
          )
          .pipe(
            tap((result) => {
              this.availableHoursArray.push(result as AvailableHours);
              this.availableHoursUpdated.next(this.availableHoursArray);
            })
          );
      })
    );
  }
  checkForOverlap(hoursArray: AvailableHours[], hours: AvailableHours) {
   
    const myArray = hoursArray.filter((item) => {
      return item.repeating_day_of_week == hours.repeating_day_of_week;
    });
    for (let i = 0; i < myArray.length; i++) {
      
      if(this.haveOverlap(hours, myArray[i]))
      {
        return true;
      }
      
    }

    return false;
  }
  haveOverlap(hours1:AvailableHours, hours2:AvailableHours):boolean
  {
    const hours1FromInt = AvailableHours.getMilitaryFormatInt(
      hours1.time_from
    );
    const hours1ToInt = AvailableHours.getMilitaryFormatInt(hours1.time_to);
    const hours2FromInt = AvailableHours.getMilitaryFormatInt(hours2.time_from);
    const hours2ToInt = AvailableHours.getMilitaryFormatInt(hours2.time_to);
    if (
      (hours1FromInt >= hours2FromInt && hours1FromInt < hours2ToInt) ||
      (hours1ToInt > hours2FromInt && hours1ToInt <= hours2ToInt) ||
      (hours1FromInt <= hours2FromInt && hours1ToInt >= hours2ToInt)
    ) {
      return true;
    }
    return false;
  }
  toggleDisabledAvailableHours(id) {
    let url = "calendar/toggleDisabledAvailableHours/" + id;
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        return this.http
          .post(this.env.API_URL + url, null, {
            headers: {
              Authorization: token["token_type"] + " " + token["access_token"],
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          })
          .pipe(
            tap((result) => {
              this.availableHoursArray.find(
                (hour) => hour.id == id
              ).disabled = (result as AvailableHours).disabled;
              this.availableHoursUpdated.next(this.availableHoursArray);
            })
          );
      })
    );
  }
  removeAvailableHours(id) {
    let url = "calendar/removeAvailableHours/" + id;
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        return this.http
          .post(this.env.API_URL + url, null, {
            headers: {
              Authorization: token["token_type"] + " " + token["access_token"],
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          })
          .pipe(
            tap((result) => {
              this.availableHoursArray.splice(
                this.availableHoursArray.indexOf(
                  this.availableHoursArray.find((hour) => hour.id == id)
                ),
                1
              );
              this.availableHoursUpdated.next(this.availableHoursArray);
            })
          );
      })
    );
  }
  emptyCalendar() {
    this.availableHoursArray = [];
    this.availableHoursUpdated.next(this.availableHoursArray);
  }
  getAvailableHours(providerId: number = null) {
    let body = null;
    let url = "calendar/availableHours";
    if (providerId != null) {
      body = {
        provider_id: providerId,
      };
    }
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        return this.http
          .post(this.env.API_URL + url, body, {
            headers: {
              Authorization: token["token_type"] + " " + token["access_token"],
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          })
          .pipe(
            tap((result) => {
              this.availableHoursArray = result as AvailableHours[];
              this.availableHoursUpdated.next(this.availableHoursArray);
            })
          );
      })
    );
  }
}
